"use client";

import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

export default function BarChartCard({ title, data }: any) {
  return (
    <div
      className="rounded-lg border p-6 shadow-sm h-64"
      style={{
        background: "var(--color-bg-neutral-50)",
        borderColor: "var(--color-line-blue-100)",
      }}
    >
      <h3
        className="text-sm font-semibold mb-4"
        style={{ color: "var(--color-text-primary)" }}
      >
        {title}
      </h3>

      <ResponsiveContainer width="100%" height="100%">
        <BarChart data={data}>
          <XAxis dataKey="city" stroke="var(--color-text-muted)" />
          <YAxis stroke="var(--color-text-muted)" />
          <Tooltip />
          <Bar
            dataKey="value"
            fill="var(--color-btn-primary-500)"
            radius={[6, 6, 0, 0]}
          />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
}
