"use client";

import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

export default function LineChartCard({ title, data }: any) {
  return (
    <div
      className="rounded-lg border p-6 shadow-sm h-64"
      style={{
        background: "var(--color-bg-neutral-50)",
        borderColor: "var(--color-line-blue-100)",
      }}
    >
      <h3
        className="text-sm font-semibold mb-4"
        style={{ color: "var(--color-text-primary)" }}
      >
        {title}
      </h3>

      <ResponsiveContainer width="100%" height="100%">
        <LineChart data={data}>
          <XAxis dataKey="month" stroke="var(--color-text-muted)" />
          <YAxis stroke="var(--color-text-muted)" />
          <Tooltip />
          <Line
            type="monotone"
            dataKey="value"
            stroke="var(--color-btn-primary-500)"
            strokeWidth={2}
            dot={{ r: 4, fill: "var(--color-btn-primary-500)" }}
          />
        </LineChart>
      </ResponsiveContainer>
    </div>
  );
}
