"use client";

import {
  PieChart,
  Pie,
  Tooltip,
  Cell,
  ResponsiveContainer,
} from "recharts";

export default function PieChartCard({ title, data }: any) {
  const COLORS = [
    "var(--color-line-blue-200)",
    "var(--color-btn-primary-500)",
    "var(--color-success-line)",
    "var(--color-warning-line)",
    "var(--color-error-line-secondary)",
  ];

  return (
    <div
      className="rounded-lg border p-6 shadow-sm h-64"
      style={{
        background: "var(--color-bg-neutral-50)",
        borderColor: "var(--color-line-blue-100)",
      }}
    >
      <h3
        className="text-sm font-semibold mb-4"
        style={{ color: "var(--color-text-primary)" }}
      >
        {title}
      </h3>

      <ResponsiveContainer width="100%" height="100%">
        <PieChart>
          <Pie
            data={data}
            innerRadius={40}
            outerRadius={70}
            paddingAngle={5}
            dataKey="value"
          >
            {data.map((_, i) => (
              <Cell key={i} fill={COLORS[i]} />
            ))}
          </Pie>
          <Tooltip />
        </PieChart>
      </ResponsiveContainer>
    </div>
  );
}
