"use client";

export default function RecentActivity({ items }: any) {
  return (
    <div
      className="rounded-lg border p-6 shadow-sm w-full"
      style={{
        background: "var(--color-bg-neutral-50)",
        borderColor: "var(--color-line-blue-100)",
      }}
    >
      <h3
        className="text-sm font-semibold mb-4"
        style={{ color: "var(--color-text-primary)" }}
      >
        Recent Activity
      </h3>

      <div className="space-y-4">
        {items.map((item: any, i: number) => (
          <div key={i} className="flex items-start gap-3">
            <div
              className="w-2 h-2 rounded-full mt-2"
              style={{ background: "var(--color-btn-primary-500)" }}
            />
            <div>
              <p style={{ color: "var(--color-text-primary)" }}>
                {item.message}
              </p>
              <p
                className="text-xs mt-1"
                style={{ color: "var(--color-text-muted)" }}
              >
                {item.time}
              </p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
